.post-item {
  display: flex;
  align-items: center;
  position: relative;
  gap: 30px;
  margin-bottom: 20px;
}
.post-item.post-item--sm {
  width: calc(33.33% - 20px);
}
.post-item.post-item--md {
  width: calc(50% - 20px);
}
.post-item.post-item--lg {
  width: 100%;
}
.post-item.row {
  flex-direction: row;
  gap: 24px;
}
.post-item.row .post-item__image {
  width: calc(50% - 20px);
}
.post-item.row .post-item__info {
  width: calc(50% - 10px);
}
.post-item.column {
  flex-direction: column;
  gap: 32px;
}
.post-item__image {
  max-width: 100%;
  height: 250px;
}
.post-item__image img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 20%;
     object-position: 50% 20%;
}
.post-item__info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.post-item__info-author {
  display: flex;
  gap: 20px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.45;
  color: #6941C6;
}
.post-item__info-author span {
  position: relative;
}
.post-item__info-author span:first-child::after {
  content: "";
  display: block;
  position: absolute;
  left: calc(100% + 10px);
  top: 50%;
  background-color: #6941C6;
  width: 5px;
  height: 5px;
  border-radius: 100%;
  transform: translate(-50%, -50%);
}
.post-item__info-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  font-weight: 600;
  margin-top: 12px;
}
.post-item__info-text {
  font-size: 16px;
  font-weight: 400;
  color: var(--blog-text-color);
  margin-top: 12px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.post-item__info-tags {
  margin-top: 24px;
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
.post-item__info-tags span {
  padding: 2px 10px;
  border-radius: 16px;
}
.post-item__info-tags .color1 {
  background-color: #F9F5FF;
  color: #6941C6;
}
.post-item__info-tags .color2 {
  background-color: #EEF4FF;
  color: #3538CD;
}
.post-item__info-tags .color3 {
  background-color: #FDF2FA;
  color: #C11574;
}
.post-item__info-tags .color4 {
  background-color: #ECFDF3;
  color: #027A48;
}
.post-item__info-tags .color5 {
  background-color: #FFF6ED;
  color: #C4320A;
}
.post-item__info-tags .color6 {
  background-color: #F8F9FC;
  color: #363F72;
}
.post-item__info-actions span {
  display: flex;
  align-items: center;
  gap: 5px;
  color: #bdbdbd;
  font-size: 1.2em;
}
.post-item .edit-btns {
  position: absolute;
  right: 20px;
  top: 20px;
  display: flex;
  align-items: center;
  gap: 15px;
  color: #d7d7d7;
  font-size: 1.2em;
}
.post-item .edit-btns button {
  background-color: transparent;
  border: none;
  color: #d7d7d7;
  font-size: 1.2em;
  cursor: pointer;
}

.skeleton {
  width: calc(33.3% - 20px);
}
.skeleton-info {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.skeleton-user {
  display: flex;
  gap: 15px;
}
.skeleton-tags {
  display: flex;
  gap: 10px;
}

.post-img {
  max-width: 250px;
}

.posts-w {
  max-width: 850px;
  height: 100%;
}/*# sourceMappingURL=postItem.css.map */