@import 'https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap';
:root {
  --background-color: #fff;
  --text-color: #1A1A1A;
  --border-color: rgb(0 0 0 / 34%);
  --blog-text-color: #667085;
  --comment-border-color: #fff;
}

[data-theme=dark] {
  --background-color: #090D1F;
  --text-color: #fff;
  --border-color: rgb(255 255 255 / 54%);
  --blog-text-color: #C0C5D0;
  --comment-border-color: rgb(255 255 255 / 54%);
}
[data-theme=dark] .switch-toggles::after {
  left: 15px !important;
  right: auto !important;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  transition: background-color 0.3s, color 0.3s;
}

h3,
h4 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  transition: all 0.2s linear;
}
a:hover {
  opacity: 0.8;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 30px;
}

.flex {
  display: flex;
}

.wrap {
  flex-wrap: wrap;
}

.posts__wrapper {
  gap: 30px;
  margin-top: 50px;
}

.forms {
  height: 100vh;
  position: relative;
}
.forms__wrapper {
  display: flex;
}
.forms__illustration {
  width: 50%;
  background-color: #6EABCD;
  padding-top: 100px;
}
.forms__illustration-txt {
  padding: 0 50px;
  margin-bottom: 50px;
}
.forms .form {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.forms .form-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 400px;
}
.forms .btn {
  width: 100%;
}
.forms .field {
  width: 100%;
}
.forms .switch-form {
  margin-top: 12px;
  display: block;
  font-size: 13px;
}
.forms i {
  text-decoration: underline;
  font-weight: 600;
  cursor: pointer;
  font-style: normal;
}

.btn {
  height: 50px;
  border-radius: 5px;
  font-size: 16px;
  min-width: 120px;
  transition: all 0.2s ease-in-out;
  background-color: transparent;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.btn.btn-primary {
  background-color: var(--text-color);
  color: var(--background-color);
  border: none;
  font-weight: 600;
}
.btn.btn-primary:hover {
  color: var(--background-color);
  background-color: var(--text-color);
}
.btn.btn-outlined {
  height: 40px;
  border: 1px solid var(--border-color);
}
.btn.btn-outlined:hover {
  color: var(--background-color);
  background-color: var(--text-color);
}

.password {
  position: relative;
}
.password .btn-showpassword {
  position: absolute;
  top: 25%;
  right: 2%;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 14px;
  color: var(--text-color);
}

.post-item.post-item--sm.row .post-item__info-author {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
.post-item.post-item--sm.row .post-item__info-author * {
  width: 100%;
}
.post-item.post-item--sm.row .post-item__image {
  height: 100%;
  max-height: 250px;
}

.pagination {
  display: flex;
  align-items: center;
  gap: 10px;
}

.footer {
  padding: 30px 0;
}
.footer .cta {
  display: flex;
  align-items: center;
  gap: 15px;
  color: var(--text-color);
  border-top: solid 1px var(--border-color);
}
.footer-cta:hover {
  text-decoration: underline;
}

@media screen and (width <= 1199px) {
  .hero-grid__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  }
  .post-item.post-item--md {
    width: 100%;
  }
  .post-item.post-item--md .post-item__image {
    height: 100%;
    max-height: 300px;
  }
  .post-item.post-item--lg {
    width: 100%;
  }
  .post-item.post-item--lg .post-item__image {
    height: 100%;
    max-height: 400px;
  }
  .post-item.post-item--sm.row {
    width: calc(50% - 20px);
  }
  .post-item.post-item--sm.row .post-item__image {
    height: 100%;
    max-height: 90%;
  }
}
@media screen and (width <= 1024px) {
  .post-item.post-item--sm.row {
    width: 100%;
  }
  .title--3xl {
    font-size: 120px;
  }
  .post-item.post-item--md .post-item__image {
    height: 100%;
    max-height: 200px;
  }
}/*# sourceMappingURL=index.css.map */