.post-item {
    display: flex;
    align-items: center;
    position: relative;
    gap: 30px;
    margin-bottom: 20px;

    &.post-item--sm {
        width: calc(33.33% - 20px);
    }

    &.post-item--md {
        width: calc(50% - 20px);
    }

    &.post-item--lg {
        width: 100%;
    }

    &.row {
        flex-direction: row;
        gap: 24px;

        .post-item__image {
            width: calc(50% - 20px);
        }

        .post-item__info {
            width: calc(50% - 10px);
        }
    }

    &.column {
        flex-direction: column;
        gap: 32px;
    }

    &__image {
        max-width: 100%;
        height: 250px;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 50% 20%;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 12px;

        &-author {
            display: flex;
            gap: 20px;
            font-size: 14px;
            font-weight: 600;
            line-height: 1.45;
            color: #6941C6;

            span {
                position: relative;

                &:first-child{
                    &::after {
                        content: '';
                        display: block;
                        position: absolute;
                        left: calc(100% + 10px);
                        top: 50%;
                        background-color: #6941C6;
                        width: 5px;
                        height: 5px;
                        border-radius: 100%;
                        transform: translate(-50%, -50%);
                    }
                }
            }
        }

        &-title {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 24px;
            font-weight: 600;
            margin-top: 12px;
        }

        &-text {
            font-size: 16px;
            font-weight: 400;
            color: var(--blog-text-color);
            margin-top: 12px;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }

        &-tags {
            margin-top: 24px;
            display: flex;
            flex-wrap: wrap;
            gap: 5px;

            span {
                padding: 2px 10px;
                border-radius: 16px;
            }

            .color1 {
                background-color: #F9F5FF;
                color: #6941C6;
            }

            .color2 {
                background-color: #EEF4FF;
                color: #3538CD;
            }

            .color3 {
                background-color: #FDF2FA;
                color: #C11574;
            }

            .color4 {
                background-color: #ECFDF3;
                color: #027A48;
            }

            .color5 {
                background-color: #FFF6ED;
                color: #C4320A;
            }

            .color6 {
                background-color: #F8F9FC;
                color: #363F72;
            }
        }

        &-actions span {
            display: flex;
            align-items: center;
            gap: 5px;
            color: #bdbdbd;
            font-size: 1.2em;
        }
    }

    .edit-btns {
        position: absolute;
        right: 20px;
        top: 20px;
        display: flex;
        align-items: center;
        gap: 15px;
        color: #d7d7d7;
        font-size: 1.2em;

        button {
            background-color: transparent;
            border: none;
            color: #d7d7d7;
            font-size: 1.2em;
            cursor: pointer;
        }
    }
}

.skeleton {
    width: calc(33.3% - 20px);

    &-info {
        display: flex;
        flex-direction: column;
        gap: 15px;
    }

    &-user {
        display: flex;
        gap: 15px;
    }

    &-tags {
        display: flex;
        gap: 10px;
    }
}

.post-img {
    max-width: 250px;
}

.posts-w {
    max-width: 850px;
    height: 100%;
}
